.image-grid {
  --gap: 3px;
  --num-cols: 4;
  --row-height: 150px;

  box-sizing: border-box;
  padding: var(--gap);

  display: grid;
  grid-template-columns: repeat(var(--num-cols), 1fr);
  grid-auto-rows: var(--row-height);
  grid-gap: var(--gap);
  gap: var(--gap);
}

.image-grid > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-grid > img:nth-child(1) {
  grid-column: span 2;
  grid-row: span 2;
}

