/* Anything udner 1024px */
@media screen and (max-width: 1024px) {
  .image-grid {
    --num-cols: 2;
    --row-height: 150px;
  }
}
@media screen and (max-width: 500px) {
  .image-grid {
    width: 98vw;
    margin: auto;
  }
}

