.container-slider {
  border: 1px solid darkblue;
  /* height: 300px; */
  /* margin: 100px auto 0; */
  position: relative;
  overflow-y: "hidden";
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}
@media screen and (max-width: 600px) {
  .container-slider {
    width: 100% !important;
    height: 200px !important;
  }
}
/* @media screen and (min-width: 500px) {
  .container-slider {
    height: "5000px !important";
  }
} */
.slide {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
  /* overflow: hidden; */
}
.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.active-anim {
  opacity: 1;
}

.btn-slide {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  /* background: #f1f1f1; */
  border: 1px solid rgba(34, 34, 34, 0.287);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
}

.btn-slide img {
  width: 25px;
  height: 25px;
  pointer-events: none;
}
.prev {
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
}
.next {
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}
